import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ModalVerRestaurantePage } from '../../pages/modal-ver-restaurante/modal-ver-restaurante.page';

@Component({
  selector: 'app-restaurante-horizontal',
  templateUrl: './restaurante-horizontal.component.html',
  styleUrls: ['./restaurante-horizontal.component.scss'],
})
export class RestauranteHorizontalComponent implements OnInit {
  @Input() restaurante:any;

  constructor(private modalCtrl: ModalController) { }

  ngOnInit() {}

 
  async mostrarModalVerRestaurante() {
    const modal = await this.modalCtrl.create({
      component: ModalVerRestaurantePage,
      componentProps: {
        restaurante: this.restaurante,
      }
    });
    await modal.present();
    // const resp = await modal.onDidDismiss();
    // console.log(resp);
  }

}
